import { INPUT_FOCUS, INPUT_BLUR, HEADER_LIST,MOUSE_ACTION,CHANGE_PAGE } from "./constants";
import { fromJS } from "immutable";  //确保源sotre不被修改
import axios from "axios"; //ajax通讯

const changeList = (data) => ({
    type: HEADER_LIST,
    data: fromJS(data),
    pageSize: Math.ceil(data.length / 10)
})

export const searchFocus = () => ({
    type: INPUT_FOCUS,
    value: true,
})

export const searchBlur = () => ({
    type:  INPUT_BLUR,
    value: false,
})

export const mouseAction = (value) => ({
    type:  MOUSE_ACTION,
    value: value
})

export const changePage = (page) => ({
    type:  CHANGE_PAGE,
    page: page
})

export const getList = () => {
    return (dispatch) => {
        axios.get('https://www.easy-mock.com/mock/5ce4973e45fff35246fea95e/jianshu/headerList').then((res)=>{
            if ( res.data.success ) {
                dispatch(changeList(res.data.data))
            }
        })
    }
}

