import axios from "axios";
import { fromJS } from "immutable";
import * as constants from "./constants";

export const Login = (account, password,history) => {
  return (dispatch) => {
    axios.get('https://www.easy-mock.com/mock/5ce4973e45fff35246fea95e/jianshu/login?account=' + account + '&password=' + password).then((res) => {
      if (res.data.success) {
        history.push('/')
        dispatch({
          type: constants.LOGIN_BOOLER,
          data: res.data
        })
      }
    })
  }
}