import { INPUT_FOCUS, INPUT_BLUR, HEADER_LIST, MOUSE_ACTION,CHANGE_PAGE } from "./constants";
import { fromJS } from "immutable";  //确保源state不被更改

const defaultState = fromJS({
    focused: false,
    mouseIn: false,
    list: [],
    page: 1,
    pageSize: 1
});

export default (state = defaultState, action) => {
    switch (action.type) {
        case INPUT_FOCUS: case INPUT_BLUR:
            return state.set('focused', action.value)  //改变state值语法
        case HEADER_LIST:
            return state.merge({      //改变多个值
                list:  action.data,
                pageSize: action.pageSize
            })
        case MOUSE_ACTION:
            return state.set('mouseIn', action.value)
        case CHANGE_PAGE:
            return state.set('page', action.page)
        default:
            return state;
    }
}