import React, { Component } from 'react';
import { connect } from "react-redux";
import { CSSTransition } from 'react-transition-group'
import { action } from "./store";
import { Link } from "react-router-dom";
import {
    HeaderWrapper,
    Logo,
    Nav,
    NavItem,
    NacSearch,
    Addition,
    Button,
    SerachWrapper,
    SearchInfo,
    SearchInfoTitle,
    SearchInfoSwitch,
    SearchInfoItem,
} from "./style";

class Header extends Component {

    getListArea = () => {
        const { focused, list, page, pageSize, handleMouseEnter, handleMouseLeave, mouseIn, handleSwitchClick } = this.props;
        const pageList = []
        const newList = list.toJS()

        if (newList.length) {
            for (let i = (page - 1) * 10; i < page * 10; i++) {
                pageList.push(
                    <SearchInfoItem key={newList[i]}>{newList[i]}</SearchInfoItem>
                )
            }
        }

        if (focused || mouseIn) {
            return (
                <SearchInfo
                    onMouseEnter={handleMouseEnter}
                    onMouseLeave={handleMouseLeave}
                >
                    <SearchInfoTitle>
                        热门搜索
                    <SearchInfoSwitch onClick={() => handleSwitchClick(page, pageSize, this.spinIcon)} >
                            <span ref={(icon) => { this.spinIcon = icon }} className="iconfont spin">&#xe851;</span>换一批
                    </SearchInfoSwitch>
                    </SearchInfoTitle>
                    <div>
                        {pageList}
                    </div>
                </SearchInfo>
            )
        } else {
            return null
        }
    }

    render() {
        const { focused, handleInputFocus, handleInputBlur, list, login } = this.props
        return (
            <HeaderWrapper>
                <Link to='/'>
                    <Logo />
                </Link>
                <Nav>
                    <NavItem className='left'>首页</NavItem>
                    <NavItem className='left'>下载App</NavItem>
                    {
                        login ? <NavItem className='right'>退出</NavItem> :
                            <Link to='/login'>
                                <NavItem className='right'>登陆</NavItem>
                            </Link>
                    }
                    <NavItem className='right'>
                        <span className="iconfont">&#xe636;</span>
                    </NavItem>
                    <CSSTransition
                        in={focused}
                        timeout={200}
                        classNames='slide'
                    >
                        <SerachWrapper>
                            <NacSearch
                                onFocus={() => handleInputFocus(list)}
                                onBlur={handleInputBlur}
                                className={focused ? 'focused' : ''}
                            />
                            <span className={focused ? 'focused iconfont' : 'iconfont'}>&#xe74b;</span>
                            {this.getListArea()}
                        </SerachWrapper>
                    </CSSTransition>
                </Nav>
                <Addition>
                    <Button className='writting'><span className="iconfont">&#xe624;</span> 写文章</Button>
                    <Button className='reg'>注册</Button>
                </Addition>
            </HeaderWrapper>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        focused: state.getIn(['header', 'focused']),  // immutable 获取state值语法
        list: state.getIn(['header', 'list']),
        page: state.getIn(['header', 'page']),
        pageSize: state.getIn(['header', 'pageSize']),
        mouseIn: state.getIn(['header', 'mouseIn']),
        login: state.getIn(['login', 'login']),
    }
}

const mapDispathcToProps = (dispatch) => {
    return {
        handleInputFocus(list) {
            (list.size === 0) && dispatch(action.getList())
            dispatch(action.searchFocus())
        },
        handleInputBlur() {
            dispatch(action.searchBlur())
        },
        handleMouseEnter() {
            dispatch(action.mouseAction(true))
        },
        handleMouseLeave() {
            dispatch(action.mouseAction(false))
        },
        handleSwitchClick(page, pageSize, icon) {
            let originAngle = icon.style.transform.replace(/[^0-9]/ig, '')
            if (originAngle) {
                originAngle = parseInt(originAngle, 10)
            } else {
                originAngle = 0
            }

            icon.style.transform = 'rotate(' + (originAngle + 360) + 'deg)'

            if (page < pageSize) {
                dispatch(action.changePage(page + 1))
            } else {
                dispatch(action.changePage(1))
            }
        },
    }
}

export default connect(mapStateToProps, mapDispathcToProps)(Header);