export default (state = defaultState, action) => {
  switch (action.type) {
    case 'get_writer_list':
      return state.set('WriterListData', fromJS(action.data))  //单个赋值 set 多个赋值merge({})
    case 'scroll_action':
      return state.set('showScroll', fromJS(action.value))  //单个赋值 set 多个赋值merge({})
    default:
      return state;
  }
}